  margin: 64px 32px;
  padding: 24px 32px;
  background-color: #EEE;
  border-radius: 3px;
  box-shadow: 0 1px 3px 1px rgba(76, 76, 76, 0.26);
  z-index: 999999;
  display: block;
  cursor: pointer;
  max-width: 480px;
}

.notification i, .notification span {
  display: table-cell;
}

.notification i {
  font-size: 2em;
  padding-right: 24px;
}

.notification span {
  vertical-align: middle;
}

.notification.pos {
  background-color: #52A256;
  color: #EEE;
}

.notification.neg {
  background-color: #E84F4F;
  color: #EEE;
}

.notification.warning {
  background-color: #e27b41;
  color: #EEE;
}

.loading-container {
  position: relative;
  display: block;
  height: 10px;
  margin: 32px auto;
}

.loading-container > div {
  width: 10px;
  height: 10px;
  border-radius: 10px;
  display: inline-block;
  vertical-align: top;
  transform: translate3d(0, 0, 0);
  animation-name: loadingBob;
  animation-duration: 1.4s;
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(0.62, 0.28, 0.23, 0.99);
  margin-right: 4px;
  background-color: #0288D1;
  animation-delay: 0.3s;
}

.loading-container > div:first-child {
  left: -16px;
  background-color: #009688;
  animation-delay: 0s;
}

.loading-container > div:last-child {
  left: 16px;
  background-color: #ef7c3c;
  animation-delay: 0.6s;
}

.search-results > h3 a {
  font-size: 0.66em;
  color: #0288D1;
  padding-left: 16px;
}

.search-results > h3 a i {
  padding-right: 12px;
}

#back-to-top {
  background-color: #0288D1;
  position: fixed;
  bottom: 16px;
  right: 24px;
  padding: 6px 12px;
  cursor: pointer;
  color: #FFF;
  width: 40px;
  height: 40px;
  border-radius: 40px;
  transition: all ease-in-out 180ms;
  opacity: 0;
  z-index: 999;
  overflow: hidden;
}

#back-to-top:hover {
  width: 136px;
  opacity: 1 !important;
}

#back-to-top:hover span {
  display: inline-block;
}

#back-to-top .inner {
  width: 136px;
}

#back-to-top i {
  margin: 0;
  font-size: 28px;
  padding: 0 12px 0 0;
}

#back-to-top span {
  line-height: 12px;
  position: relative;
  top: -5px;
}

.contained-search-box {
  display: flex;
}

.contained-search-box input, .contained-search-box button {
  border-radius: 0;
  border: 1px solid #DDD;
  margin-left: -1px;
}

.contained-search-box input {
  flex: 5;
}

.contained-search-box input:focus, .contained-search-box input:active {
  outline: 0;
}

.contained-search-box button {
  width: 60px;
}

.contained-search-box button i {
  padding: 0;
}

.contained-search-box button.cancel.active {
  background-color: #E84F4F;
  color: #EEE;
}

.entity-selector {
  border: 1px solid #DDD;
  border-radius: 3px;
  overflow: hidden;
  font-size: 0.8em;
}

.entity-selector input[type="text"] {
  width: 100%;
  display: block;
  border-radius: 0;
  border: 0;
  border-bottom: 1px solid #DDD;
  font-size: 16px;
  padding: 12px 16px;
}

.entity-selector .entity-list {
  overflow-y: scroll;
  height: 400px;
  background-color: #EEEEEE;
}

.entity-selector .loading {
  height: 400px;
  padding-top: 24px;
}

.entity-selector .entity-list > p {
  text-align: center;
  padding-top: 24px;
  font-size: 1.333em;
}

.entity-selector .entity-list > div {
  padding-left: 16px;
  padding-right: 16px;
  background-color: #FFF;
  transition: all ease-in-out 120ms;
  cursor: pointer;
}

.entity-list-item.selected h3, .entity-list-item.selected i, .entity-list-item.selected p, .entity-list-item.selected a, .entity-list-item.selected span {
  color: #EEE;
}

.center-box {
  margin: 32px auto 0 auto;
  padding: 16px 48px 32px 48px;
  width: 420px;
  max-width: 100%;
  display: inline-block;
  text-align: left;
  vertical-align: top;
}

.center-box input {
  width: 100%;
}

.center-box.login {
  background-color: #EEE;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #DDD;
}
